import React, { useState } from "react";
import AddCourseComponent from "./AddCourseComponent";
import { useDispatch, useSelector } from "react-redux";
import { courseOperations } from "app/store/state/active-course";

function AddCourse(props) {
  const dispatch = useDispatch();
  const tinyMceApiKey = useSelector((state) => state.app.tinyMceApiKey);
  const [uploadMode, setUploadMode] = useState(false);

  const [courseData, setCourseData] = useState({
    name: "",
    overview: "",
    slug: "",
    url: "",
    price: 0,
    author: "",
    featuredImage: "",
    isPublished: false,
    level: "beginner",
    type: "Rich text",
    freeFlag: true,
  });

  const toggleUploadMode = (e) => {
    e.stopPropagation();
    setUploadMode(!uploadMode);
  };

  const onImageUpload = (imgUrl) => {
    setCourseData({
      ...courseData,
      featuredImage: imgUrl,
    });
    setUploadMode(false);
  };

  const handleChange = (field, e) => {
    e.stopPropagation();
    if (field) {
      const newValue = e.target.value;
      const oldValue = courseData[field];
      if (newValue !== oldValue) {
        setCourseData({
          ...courseData,
          [field]: newValue,
        });
      }
    }
  };

  const handleSwitchChange = (field, e) => {
    e.stopPropagation();
    if (field) {
      const newValue = e.target.checked;
      const oldValue = courseData[field];
      if (newValue !== oldValue) {
        setCourseData({
          ...courseData,
          [field]: newValue,
        });
      }
    }
  };

  const handleEditorChange = (field, overview) => {
    setCourseData({
      ...courseData,
      [field]: overview,
    });
  };

  const reset = () => {
    setCourseData({
      name: "",
      overview: "",
      slug: "",
      url: "",
      price: 0,
      author: "",
      featuredImage: "",
      isPublished: false,
      level: "beginner",
      type: "Rich text",
      freeFlag: true,
    });
  };

  const saveCourseDetails = (e) => {
    e.preventDefault();
    reset();
    dispatch(courseOperations.saveNewCourseDetails(courseData));
  };

  return (
    <AddCourseComponent
      courseData={courseData}
      tinyMceApiKey={tinyMceApiKey}
      saveCourseDetails={saveCourseDetails}
      uploadMode={uploadMode}
      toggleUploadMode={toggleUploadMode}
      onImageUpload={onImageUpload}
      handleSwitchChange={handleSwitchChange}
      handleChange={handleChange}
      handleEditorChange={handleEditorChange}
    />
  );
}

export default AddCourse;
