import React from "react";
import Card from "react-bootstrap/Card";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBook,
  faBookOpen,
  faSitemap,
  faMoneyCheckAlt,
  faChalkboardTeacher,
  faImage,
  faSave,
  faLayerGroup,
  faLaptopCode,
} from "@fortawesome/free-solid-svg-icons";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import UploadImageModal from "app/views/common/components/UploadImageModal";
import { Editor } from "@tinymce/tinymce-react";
import { uploadImageFromBlobInfo } from "app/utils";
import { LEVELS_MAP } from "app/constants/levels";
import EDITOR_LANGUAGE_CONFIG from "app/constants/tiny-mce-languages";

function AddCourseComponent(props) {
  const {
    courseData: {
      name,
      overview,
      slug,
      freeFlag,
      price,
      author,
      level,
      type,
      featuredImage,
    },
    tinyMceApiKey,
    saveCourseDetails,
    toggleUploadMode,
    onImageUpload,
    handleSwitchChange,
    handleChange,
    uploadMode,
    handleEditorChange,
  } = props;

  return (
    <div id="cfg_add_course_page" className="page p-0">
      <UploadImageModal
        visible={uploadMode}
        onCancel={toggleUploadMode}
        onImageUpload={onImageUpload}
      />
      <Form onSubmit={saveCourseDetails}>
        <div className="course-info p-3">
          <Card className="border-0">
            <Card.Body>
              <div className="text-muted">
                <div className="border-bottom mb-0 pb-3">
                  <strong className="d-block text-gray-dark">
                    <FontAwesomeIcon icon={faBook} className="mr-2" />
                    Title
                  </strong>
                  <Form.Group controlId="name" className="mb-0 mt-2">
                    <Form.Control
                      type="text"
                      value={name || ""}
                      placeholder="Enter title"
                      onChange={(e) => handleChange("name", e)}
                      required
                    />
                  </Form.Group>
                </div>
              </div>
              <div className="text-muted pt-3">
                <div className="border-bottom mb-0 pb-3">
                  <strong className="d-block text-gray-dark">
                    <FontAwesomeIcon icon={faBookOpen} className="mr-2" />
                    Overview
                  </strong>

                  <Form.Group controlId="overview" className="mb-0 mt-2">
                    <Editor
                      apiKey={tinyMceApiKey}
                      key={"form-wvgiwys"}
                      value={overview || ""}
                      init={{
                        selector: "textarea",
                        images_upload_handler: function (
                          blobInfo,
                          success,
                          failure
                        ) {
                          uploadImageFromBlobInfo(blobInfo, success, failure);
                        },
                        height: 500,
                        menubar: false,
                        plugins: [
                          "advlist codesample autolink lists link image imagetools charmap print preview anchor",
                          "searchreplace visualblocks code fullscreen",
                          "insertdatetime media table paste code help wordcount",
                        ],
                        toolbar: `undo redo | formatselect | image | codesample | bold italic backcolor | 
             alignleft aligncenter alignright alignjustify | 
             bullist numlist outdent indent | removeformat | help`,
                        codesample_languages: EDITOR_LANGUAGE_CONFIG,

                        entity_encoding: "raw",
                      }}
                      onEditorChange={(overview) =>
                        handleEditorChange("overview", overview)
                      }
                    />
                  </Form.Group>
                </div>
              </div>
              <div className="text-muted pt-3">
                <div className="border-bottom mb-0 pb-3">
                  <strong className="d-block text-gray-dark">
                    <FontAwesomeIcon icon={faSitemap} className="mr-2" />
                    Slug
                  </strong>
                  <Form.Group controlId="slug" className="mb-0 mt-2">
                    <Form.Control
                      type="text"
                      value={slug || ""}
                      placeholder="Enter slug"
                      onChange={(e) => handleChange("slug", e)}
                      required
                    />
                  </Form.Group>
                </div>
              </div>
              <div className="text-muted pt-3">
                <div className="border-bottom mb-0 pb-3">
                  <strong className="d-block text-gray-dark">
                    <FontAwesomeIcon icon={faMoneyCheckAlt} className="mr-2" />
                    Pricing
                  </strong>
                  <>
                    <Form.Group controlId="price" className="mb-0 mt-2">
                      <Form.Label>Free?</Form.Label>
                      <Form.Check
                        type="switch"
                        label={freeFlag ? "Yes" : "No"}
                        id="custom-switch"
                        className="align-top ml-2"
                        checked={freeFlag}
                        onChange={(e) => handleSwitchChange("freeFlag", e)}
                        inline
                      />
                    </Form.Group>

                    {!freeFlag && (
                      <Form.Group controlId="price" className="mb-0 mt-2">
                        <Form.Control
                          type="number"
                          value={price || ""}
                          placeholder="Enter price"
                          onChange={(e) => handleChange("price", e)}
                          required
                        />
                      </Form.Group>
                    )}
                  </>
                </div>
              </div>
              <div className="text-muted pt-3">
                <div className="border-bottom mb-0 pb-3">
                  <strong className="d-block text-gray-dark">
                    <FontAwesomeIcon
                      icon={faChalkboardTeacher}
                      className="mr-2"
                    />
                    Author
                  </strong>
                  <Form.Group controlId="author" className="mb-0 mt-2">
                    <Form.Control
                      type="text"
                      value={author || ""}
                      placeholder="Enter author's name"
                      onChange={(e) => handleChange("author", e)}
                    />
                  </Form.Group>
                </div>
              </div>
              <div className="text-muted pt-3">
                <div className="border-bottom mb-0 pb-3">
                  <strong className="d-block text-gray-dark">
                    <FontAwesomeIcon icon={faLayerGroup} className="mr-2" />
                    Level
                  </strong>
                  <Form.Group controlId="level" className="mb-0 mt-2">
                    <Form.Control
                      as="select"
                      value={level}
                      onChange={(e) => handleChange("level", e)}
                      custom
                    >
                      {Object.keys(LEVELS_MAP).map((key) => (
                        <option key={key} value={key}>
                          {LEVELS_MAP[key]}
                        </option>
                      ))}
                    </Form.Control>
                  </Form.Group>
                </div>
              </div>
              <div className="text-muted pt-3">
                <div className="border-bottom mb-0 pb-3">
                  <strong className="d-block text-gray-dark">
                    <FontAwesomeIcon icon={faLaptopCode} className="mr-2" />
                    Type
                  </strong>
                  <Form.Group controlId="type" className="mb-0 mt-2">
                    <Form.Control
                      as="select"
                      value={type}
                      onChange={(e) => handleChange("type", e)}
                      custom
                    >
                      <option value={"Rich text"}>Rich text</option>
                      <option value={"Rich text and video"}>
                        Rich text and video
                      </option>
                      <option value={"Video"}>Video</option>
                    </Form.Control>
                  </Form.Group>
                </div>
              </div>
              <div className="text-muted pt-3">
                <div className="mb-0 pb-3">
                  <strong className="d-block text-gray-dark">
                    <FontAwesomeIcon icon={faImage} className="mr-2" />
                    Featured Image
                  </strong>
                  <Form.Group controlId="featuredImage" className="mb-0 mt-2">
                    <Row>
                      <Col>
                        <Button className="mb-2" onClick={toggleUploadMode}>
                          Upload
                        </Button>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <Form.Control
                          type="text"
                          readOnly={true}
                          value={featuredImage || ""}
                          placeholder="Enter image URL"
                          onChange={(e) => handleChange("featuredImage", e)}
                          required
                        />
                      </Col>
                    </Row>
                  </Form.Group>
                </div>
              </div>
            </Card.Body>
          </Card>
        </div>
        <div className="course-action-panel">
          <div className="p-3">
            <Button type="submit" variant="success" className="ml-2">
              <FontAwesomeIcon icon={faSave} className="mr-2" />
              Save
            </Button>
          </div>
        </div>
      </Form>
    </div>
  );
}

export default AddCourseComponent;
