import React, { useState } from "react";
import PropTypes from "prop-types";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import { uploadImageFromFile } from "app/utils";

function UploadImageModal({ visible, onImageUpload, onCancel }) {
  const [validation, setValidity] = useState({
    valid: false,
    message: undefined,
  });

  const [uploadInfo, setUploadInfo] = useState({
    uploading: false,
    uploadErr: false,
    uploadErrMsg: undefined,
  });

  const [file, setFile] = useState(undefined);

  const handleFileUpload = (e) => {
    const fileTarget = e.target;
    const filePath = fileTarget.value;
    const allowedExtensions = /(\.jpg|\.jpeg|\.png|\.gif)$/i;
    if (!allowedExtensions.exec(filePath)) {
      setValidity({
        valid: false,
        message:
          "Please upload file having extensions .jpeg/.jpg/.png/.gif only",
      });
      fileTarget.value = "";
      return false;
    } else {
      setFile(fileTarget.files[0]);
      setValidity({
        valid: true,
        message: undefined,
      });
      return true;
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setUploadInfo({
      uploading: true,
      uploadErr: false,
      uploadErrMsg: undefined,
    });

    uploadImageFromFile(
      file,
      (imgUrl) => {
        setUploadInfo({
          uploading: false,
          uploadErr: false,
          uploadErrMsg: undefined,
        });
        onImageUpload(imgUrl);
      },
      (message) => {
        setUploadInfo({
          uploading: false,
          uploadErr: false,
          uploadErrMsg: message,
        });
      }
    );
  };

  return (
    <Modal show={visible} centered size="lg" onHide={onCancel}>
      <Modal.Header>
        <b>Upload the featured image for this course</b>
      </Modal.Header>
      <Modal.Body>
        {!validation.valid && validation.message && (
          <div className="pb-2 text-center text-danger">
            {validation.message}
          </div>
        )}
        {uploadInfo.uploading ? (
          <div className="pb-2 text-center text-warning">Uploading</div>
        ) : uploadInfo.uploadErr ? (
          <div className="pb-2 text-center text-danger">
            {uploadInfo.uploadErrMsg}
          </div>
        ) : null}
        <Form onSubmit={handleSubmit}>
          <div className="mb-3">
            <Form.File id="formcheck-api-regular">
              <Form.File.Input name="featImg" onChange={handleFileUpload} />
            </Form.File>
          </div>
          <div className="pt-3 text-right">
            <Button type="submit" variant="danger" className="ml-2">
              Submit
            </Button>
            <Button variant="light" className="ml-2" onClick={onCancel}>
              Cancel
            </Button>
          </div>
        </Form>
      </Modal.Body>
    </Modal>
  );
}

UploadImageModal.propTypes = {
  visible: PropTypes.bool,
  onOk: PropTypes.func,
  onCancel: PropTypes.func,
};

export default UploadImageModal;
